import request from '@/utils/request'

// 获取地址列表
export const getAddressList = () => {
  return request.get('/address/list')
}

// 添加省市区
export const addAddressList = () => {
  return request({
    url: 'address/add',
    method: 'post',
    data: {
      form: {
        name: 'js',
        phone: '13234933128',
        region: [{
          value: 782,
          label: '上海'
        },
        {
          value: 783,
          label: '上海市'
        },
        {
          value: 785,
          label: '徐汇区'
        }],
        detail: '北京路1号楼8888室'
      }
    }
  })
}
